/**
 * 
 * 
 */

// 
export interface ErrorLog {
  id: string;
  timestamp: string;
  type: string;
  message: string;
  component: string;
  operation: string;
  severity: 'error' | 'warning' | 'info';
  details?: any;
  stack?: string;
}

// 
export interface AppState {
  id: string;
  timestamp: string;
  lastComponent: string;
  lastAction: string;
  lastData?: any;
}

// 
let errorLogs: ErrorLog[] = [];
const MAX_ERROR_LOGS = 50;

// 
let appStateHistory: AppState[] = [];
const MAX_APP_STATES = 20;

// ID
const generateId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substring(2);
};

/**
 * 
 * @param component 
 * @param operation 
 * @param error 
 * @param details 
 */
export const logError = (
  component: string, 
  operation: string, 
  error: Error | string,
  details?: any
): string => {
  const errorMessage = typeof error === 'string' ? error : error.message;
  const errorStack = typeof error === 'string' ? undefined : error.stack;
  const errorType = typeof error === 'string' ? 'CustomError' : error.name;
  
  const errorLog: ErrorLog = {
    id: generateId(),
    timestamp: new Date().toISOString(),
    type: errorType,
    message: errorMessage,
    component,
    operation,
    severity: 'error',
    details,
    stack: errorStack
  };
  
  // 
  errorLogs.unshift(errorLog);
  
  // 
  if (errorLogs.length > MAX_ERROR_LOGS) {
    errorLogs = errorLogs.slice(0, MAX_ERROR_LOGS);
  }
  
  // 
  if (typeof window !== 'undefined') {
    console.error(`[${component}][${operation}] ${errorMessage}`, details || '');
  }
  
  return errorLog.id;
};

/**
 * 
 * @param component 
 * @param operation 
 * @param message 
 * @param details 
 */
export const logWarning = (
  component: string, 
  operation: string, 
  message: string,
  details?: any
): string => {
  const warningLog: ErrorLog = {
    id: generateId(),
    timestamp: new Date().toISOString(),
    type: 'Warning',
    message,
    component,
    operation,
    severity: 'warning',
    details
  };
  
  // 
  errorLogs.unshift(warningLog);
  
  // 
  if (errorLogs.length > MAX_ERROR_LOGS) {
    errorLogs = errorLogs.slice(0, MAX_ERROR_LOGS);
  }
  
  // 
  if (typeof window !== 'undefined') {
    console.warn(`[${component}][${operation}] ${message}`, details || '');
  }
  
  return warningLog.id;
};

/**
 * 
 * @param component 
 * @param operation 
 * @param message 
 * @param details 
 */
export const logInfo = (
  component: string, 
  operation: string, 
  message: string,
  details?: any
): string => {
  const infoLog: ErrorLog = {
    id: generateId(),
    timestamp: new Date().toISOString(),
    type: 'Info',
    message,
    component,
    operation,
    severity: 'info',
    details
  };
  
  // 
  errorLogs.unshift(infoLog);
  
  // 
  if (errorLogs.length > MAX_ERROR_LOGS) {
    errorLogs = errorLogs.slice(0, MAX_ERROR_LOGS);
  }
  
  // 
  if (typeof window !== 'undefined') {
    console.info(`[${component}][${operation}] ${message}`, details || '');
  }
  
  return infoLog.id;
};

/**
 * 
 * @param component 
 * @param action 
 * @param data 
 */
export const logAppState = (
  component: string,
  action: string,
  data?: any
): string => {
  const appState: AppState = {
    id: generateId(),
    timestamp: new Date().toISOString(),
    lastComponent: component,
    lastAction: action,
    lastData: data
  };
  
  // 
  appStateHistory.unshift(appState);
  
  // 
  if (appStateHistory.length > MAX_APP_STATES) {
    appStateHistory = appStateHistory.slice(0, MAX_APP_STATES);
  }
  
  return appState.id;
};

/**
 * 
 * @returns 
 */
export const getErrorLogs = (): ErrorLog[] => {
  return [...errorLogs];
};

/**
 * 
 * @returns 
 */
export const getAppStateHistory = (): AppState[] => {
  return [...appStateHistory];
};

/**
 * 
 * @param id ID
 * @returns undefined
 */
export const getErrorLogById = (id: string): ErrorLog | undefined => {
  return errorLogs.find(log => log.id === id);
};

/**
 * 
 */
export const clearErrorLogs = (): void => {
  errorLogs = [];
};

/**
 * 
 */
export const clearAppStateHistory = (): void => {
  appStateHistory = [];
};

/**
 * 
 * @param count 
 * @returns 
 */
export const getRecentErrors = (count: number = 5): ErrorLog[] => {
  return errorLogs.filter(log => log.severity === 'error').slice(0, count);
};

/**
 * 
 * @param minutes 
 * @returns 
 */
export const getErrorsInLastMinutes = (minutes: number): ErrorLog[] => {
  const now = new Date();
  const timeThreshold = new Date(now.getTime() - minutes * 60000).toISOString();
  
  return errorLogs.filter(log => log.timestamp >= timeThreshold);
};

/**
 * 
 * @param error 
 * @returns 
 */
export const formatErrorMessage = (error: Error | string): string => {
  if (typeof error === 'string') return error;
  
  if (error.message.includes('network')) {
    return `${error.message}`;
  }
  
  if (error.message.includes('timeout')) {
    return `${error.message}`;
  }
  
  if (error.message.includes('permission')) {
    return `${error.message}`;
  }
  
  return error.message;
};

/**
 * 
 */
export const setupGlobalErrorHandler = (): void => {
  if (typeof window !== 'undefined') {
    // Promise
    window.addEventListener('unhandledrejection', (event) => {
      logError(
        'Global', 
        'UnhandledPromiseRejection', 
        event.reason || 'Promise',
        { promiseReason: event.reason }
      );
    });
    
    // JS
    window.addEventListener('error', (event) => {
      logError(
        'Global',
        'RuntimeError',
        event.error || event.message,
        {
          fileName: event.filename,
          lineNumber: event.lineno,
          columnNumber: event.colno
        }
      );
      
      // 
      return false;
    });
    
    console.log('');
  }
};

// 
export const errorLogger = {
  logError,
  logWarning,
  logInfo,
  logAppState,
  getErrorLogs,
  getAppStateHistory,
  clearErrorLogs,
  clearAppStateHistory,
  getErrorLogById,
  getRecentErrors,
  getErrorsInLastMinutes,
  formatErrorMessage,
  setupGlobalErrorHandler
}; 