/**
 * Supabase OR
 * (.)
 * @param searchTerm 
 * @param fields 
 * @returns OR
 */
function createSafeOrQuery(searchTerm: string, fields: string[]): string {
  if (!fields || fields.length === 0) {
    throw new Error('');
  }
  
  // ilike
  const conditions = fields.map(field => {
    // (.)SQL
    const needQuotes = field.includes('.') || 
                      /[\u4e00-\u9fa5]/.test(field) || 
                      field.match(/^(id|all|table|column|database|from|to|select|where|order|by|or|and|not|if|else)$/i);
    
    const safeField = needQuotes ? `"${field}"` : field;
      
    return `${safeField}.ilike.%${searchTerm}%`;
  });
  
  // OR: (field1.ilike.%value%,field2.ilike.%value%)
  return `(${conditions.join(',')})`;
}

