/**
 * 
 * @param startIndex 
 */
async function loadRemainingRecipes(startIndex: number): Promise<any[]> {
  let allRemainingRecipes: any[] = [];
  let page = Math.floor(startIndex / 1000);
  const pageSize = 1000;
  let hasMore = true;

  try {
    // 
    while (hasMore) {
      const start = startIndex + (page - Math.floor(startIndex / 1000)) * pageSize;
      const end = start + pageSize - 1;
      
      console.log(` ${page+1} ${start}-${end}`);
      const { data, error } = await supabase
        .from('CHrecipes')
        .select('*')
        .range(start, end);
      
      if (error) {
        console.error(` ${page+1} :`, error);
        break;
      }
      
      if (!data || data.length === 0) {
        console.log(` ${page+1} `);
        hasMore = false;
        break;
      }
      
      console.log(` ${page+1}  ${data.length} `);
      allRemainingRecipes = [...allRemainingRecipes, ...data];
      
      if (data.length < pageSize) {
        console.log(` ${page+1} `);
        hasMore = false;
      } else {
        page++;
      }
      
      // 
      await new Promise(resolve => setTimeout(resolve, 1000));
    }
    
    return allRemainingRecipes;
  } catch (error) {
    console.error(':', error);
    return [];
  }
}

