import { NextResponse } from 'next/server';
import { supabase } from '../../../utils/supabase';
import { transformDbRecipesToFrontend, DbRecipe } from '../../../utils/dataMapper';
import { logError } from '../../../utils/errorLogger';

// 
export const dynamic = 'force-dynamic';
export const runtime = 'edge';

// API
interface ApiErrorResponse {
  error: string;
  details?: string;
}

/**
 * GET
 */
export async function GET() {
  // 
  const isSSG = process.env.NODE_ENV === 'production' && typeof window === 'undefined';
  
  // 
  if (isSSG) {
    console.log('API: Supabase');
    return new NextResponse(JSON.stringify([]), {
      status: 200,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    });
  }
  
  try {
    console.log('API: Supabase...');
    console.log('Supabase URL:', process.env.NEXT_PUBLIC_SUPABASE_URL || '');
    console.log('Supabase Key:', process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY ? '' : '');
    
    // 
    try {
      console.log('Supabase...');
      const { data: testData, error: testError } = await supabase.from('CHrecipes').select('id').limit(1);
      
      if (testError) {
        console.error('Supabase:', testError);
        logError('API', 'Supabase', testError);
        throw new Error(`Supabase: ${testError.message}`);
      }
      
      console.log('Supabase...');
    } catch (connErr) {
      console.error('Supabase:', connErr);
      logError('API', 'Supabase', connErr);
      const errorResponse: ApiErrorResponse = { error: '' };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 503,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    const { data, error } = await supabase.from('CHrecipes').select('*');
    
    if (error) {
      console.error('API: Supabase:', error);
      logError('API', 'Supabase', error);
      const errorResponse: ApiErrorResponse = { error: `: ${error.message}` };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    if (!data || data.length === 0) {
      console.warn('API: ');
      return new NextResponse(JSON.stringify([]), {
        status: 200,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    console.log(`API: Supabase ${data.length} `);
    
    try {
      // 
      console.log('...');
      const transformedData = transformDbRecipesToFrontend(data as unknown as DbRecipe[]);
      console.log(` ${transformedData.length} `);
      
      // CORS
      return new NextResponse(JSON.stringify(transformedData), {
        status: 200,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    } catch (transformError) {
      console.error('API: :', transformError);
      logError('API', '', transformError);
      const errorResponse: ApiErrorResponse = { error: '' };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
  } catch (error) {
    console.error('API: :', error);
    logError('API', '', error);
    const errorResponse: ApiErrorResponse = { 
      error: '', 
      details: error instanceof Error ? error.message : String(error) 
    };
    return new NextResponse(JSON.stringify(errorResponse), {
      status: 500,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    });
  }
}

/**
 * OPTIONSCORS
 */
export async function OPTIONS() {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type'
    }
  });
} 